import React, { Fragment, useState, useEffect } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import Footer from "./components/Footer";
import AppHeader from "./components/Header";
import Cards from "./components/Content";

export default function App() {
  const classes = useStyles();

  const [state, setState] = useState({
    loading: false,
    data: [],
    error: null,
    holder: []
  });

  useEffect(() => {
    const fetchData = async () => {
      await setState({ loading: true });
      const res = await fetch(`/api/equipos/`);
      let data = await res.json();
      await setState({
        data,
        holder: data,
        loading: false
      });
    };
    fetchData();
    return () => {
      setState({ data: [] });
      console.log("desmontando");
    };
  }, []);

  const searchPhone = event => {
    const { value } = event.target;
    const newData = state.holder.filter(item => {
      const itemData = `${item.Modelo.toUpperCase()}`;
      const textData = value.toUpperCase();
      return itemData.indexOf(textData) > -1;
    });

    setState({ ...state, data: newData });
  };

  return (
    <Fragment>
      <CssBaseline />
      <AppHeader searchFunction={searchPhone} />
      <main>
        <div className={classes.content}>
          <Container maxWidth="md">
            <Typography
              component="h1"
              variant="h4"
              align="center"
              color="textPrimary"
              gutterBottom
            >
              Precios del servicio para el iPhone
            </Typography>
            <Typography
              // variant="h6"
              align="justify"
              color="textSecondary"
              className={classes.paragraph}
              paragraph
            >
              Los siguientes precios corresponden a las reparaciones de iPhone
              realizadas por Apple. Tanto los precios como los términos pueden
              variar en otros proveedores de servicios.
              {/* <br/> */}
              Es posible que la garantía, las leyes de protección al consumidor
              o AppleCare+ cubran el servicio del iPhone.
            </Typography>
          </Container>
        </div>
        <Container className={classes.cardGrid} maxWidth="md">
          {state.loading === true ? (
            <Typography color="textSecondary" align="center">
              Cargando ...
            </Typography>
          ) : (
            <Cards data={state.data} />
          )}
        </Container>
      </main>
      <Footer />
    </Fragment>
  );
}

const useStyles = makeStyles(theme => ({
  icon: {
    marginRight: theme.spacing(2)
  },
  content: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(16, 0, 8, 0),
    [theme.breakpoints.down("sm")]: {
      padding: theme.spacing(10, 0, 4, 0)
    }
  },
  paragraph: {
    paddingTop: theme.spacing(2)
  },
  cardGrid: {
    flex: 1,
    paddingTop: theme.spacing(6),
    paddingBottom: theme.spacing(6),
    [theme.breakpoints.down("sm")]: {
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2)
    }
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6)
  }
}));
