import React from "react";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import withWidth from "@material-ui/core/withWidth";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Avatar from "@material-ui/core/Avatar";

const Cards = ({ data, width }) => {
  const classes = useStyles();
  return data.length > 0 ? (
    width !== "xs" ? (
      <Grid container spacing={5}>
        {data.map(card => (
          <Item key={card.Modelo} className={classes.main} item={card} />
        ))}
      </Grid>
    ) : (
      <List className={classes.root}>
        {data.map(card => {
          return (
            <MobileItem
              key={card.Modelo}
              className={classes.main}
              item={card}
            />
          );
        })}
      </List>
    )
  ) : (
    <Typography color="textSecondary" align="center" className={classes.yolo}>
      No se encontraron resultados para la busqueda
    </Typography>
  );
};
export default withWidth()(Cards);

const Item = ({ item }) => {
  const { Modelo, Costo, avatar } = item;
  const classes = useStyles();
  return (
    <Grid item center="true" className={classes.phoneCard} md={4}>
      <Card className={classes.card}>
        <CardMedia
          // component="img"
          className={classes.cardMedia}
          image={avatar}
          title="Image title"
        />
        <CardContent className={classes.cardContent}>
          <Typography gutterBottom variant="h5" component="h2">
            {Modelo}
          </Typography>
          <Typography>Pantalla: ${Costo}</Typography>
        </CardContent>
      </Card>
    </Grid>
  );
};

const MobileItem = ({ item }) => {
  const { Modelo, Costo, avatar } = item;
  return (
    <ListItem button>
      <ListItemAvatar>
        <Avatar alt={Modelo} src={avatar} />
      </ListItemAvatar>
      <ListItemText primary={Modelo} secondary={`Pantalla: $${Costo}`} />
    </ListItem>
  );
};

const useStyles = makeStyles(theme => ({
  main: {
    display: "flex",
    flex: 1
  },
  phoneCard: {
    display: "flex",
    justifyContent: "center"
  },
  card: {
    height: "100%",
    maxWidth: 230,
    minWidth: 200,
    // display: "flex",
    // justifyContent: "center",
    flexDirection: "column"
  },
  cardMedia: {
    maxHeight: 180,
    minHeight: 150,
    paddingTop: theme.spacing(8)
    // paddingTop: "0%" // 16:9
  },
  cardContent: {
    flexGrow: 1
  },
  yolo: {
    flex: 1
  },
  root: {
    width: "100%",

    backgroundColor: theme.palette.background.paper
  }
}));
